import React from 'react';

import classes from '../Content.module.css';
import Pdf from '../../../assets/Pyarsabadi_Shivani_Resume.pdf'

const Home = () => {
return(
    <div className={classes.home}>
       <h2> About Me </h2>
       <p> Hello! I am Software Developer looking for job opportunities in India/Germany as front end or back end developer </p>
       <p> I have working experience of 2.5 years in Unisys India.</p>
       <p> My objective is to associate with an organization which progresses dynamically and 
           to be a part of the challenging team which strives for the better growth of the organization 
           and provides me with the opportunity to enhance my talent with an intention to be an asset 
           to the company.</p>
        <p> You can find my resume below </p>
        <button><a href = {Pdf} target = "_blank" rel="noopener noreferrer">View Resume</a></button>
    </div>
)
}

export default Home;