import React from 'react'
import classes from '../Content.module.css';

const Skills = () => {
    return (
        <div className={classes.home}>
            <h2> Skills </h2>
            <div>
                <p> Good knowledge on Front End Technologies like: </p>
                <ul> HTML </ul>
                <ul> CSS </ul>
                <ul> JavaScript </ul>
                <ul> React </ul>
            </div>
            <div>
                <p> Hands on experience on Back End Technologies like: </p>
                <ul> C# </ul>
                <ul> ASP.Net </ul>
                <ul> ADO.Net </ul>
                <ul> SQL </ul>
            </div>


        </div>
    )
}

export default Skills;