import React from 'react'
import classes from '../Content.module.css';

const Project = () => {
    return (
        <div className={classes.home}>
            <h2> Project Details</h2>
            <p>Project :	Financial Transaction Manager (FTM) </p> 
            <p>Organization : 	Unisys India, Bangalore </p> 
            <p>Domain: 		Banking</p> 
            <p>Designation:		Consultant 4. </p> 
            <p>Duration:		Feb 2015 – Jul 2017 (2.5 years)</p> 
            <p>Technology:		C#, ASP.NET, ADO.Net, Javascript and SQL.</p>
            <p>Environment:		Visual Studio 2015, SQL Server 2016 </p>
            <p>Description: </p> 
            <p>FTM is a Global Digital Retail Banking application designed to meet the requirement of corporate
            banking sectors. An employee of bank, whose job includes the responsibility of helping the bank
            customers with their banking needs, opening account, closing account, depositing a check or
            making a withdrawal, Personal loan and Mortgage loans. Most bank tellers are located behind a
    counter or desk at the bank, and communicate with the customer across that barrier. </p> <br />
        </div>
    );
}

export default Project;