import React from 'react';
import { NavLink } from  'react-router-dom'

import classes from './SideBar.module.css'
import image from '../../assets/05.jpg'

const SideBar = () => {
    return (
    <div className={classes.sidebar}>
        <div>
            <img src={image} alt="Shivani_Pyarsabadi"></img>
            <div>
            <h3> Shivani Pyarsabadi</h3>
            </div>
            <div>
            <NavLink to='/home' className={classes.navlink} activeStyle={{ color: 'salmon' }}>Home</NavLink>
            <NavLink to='/skills' className={classes.navlink} activeStyle={{ color: 'salmon' }}>Skills</NavLink>
            <NavLink to='/project' className={classes.navlink} activeStyle={{ color: 'salmon' }}>Project</NavLink>
            <NavLink to='/education' className={classes.navlink} activeStyle={{ color: 'salmon' }}>Education</NavLink>
            <NavLink to='/contact' className={classes.navlink} activeStyle={{ color: 'salmon' }}>Contact</NavLink>
            </div>
        </div>
    </div>
    )
}

export default SideBar;